import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_COMPANY_DROPDOWN_ASYNC,
  GET_COMPANY_DROPDOWN_FAILED,
  GET_COMPANY_DROPDOWN_SUCCESS,
  GET_STATES_ASYNC,
  GET_STATES_FAILED,
  GET_STATES_PROGRESS,
  GET_STATES_SUCCESS,
  CREATE_INFORMATION_FAILED,
  CREATE_INFORMATION_SUCCESS,
  CREATE_INFORMATION_ASYNC,
  SAVE_YOUR_EXPERIENCE_SUCCESS,
  SAVE_YOUR_EXPERIENCE_FAILED,
  SAVE_YOUR_EXPERIENCE_ASYNC,
  SAVE_YOUR_EXPERIENCE_REQUESTED,
  SAVE_WORK_REFERENCE_FAILED,
  SAVE_WORK_REFERENCE_ASYNC,
  SAVE_WORK_REFERENCE_SUCCESS,
  SAVE_WORK_REFERENCE_REQUESTED,
  GET_ONBOARDING_QUESTION_ASYNC,
  GET_ONBOARDING_QUESTION_PROGRESS,
  GET_ONBOARDING_QUESTION_SUCCESS,
  GET_ONBOARDING_QUESTION_FAILED,
  SAVE_YOUR_TEST_FAILED,
  SAVE_YOUR_TEST_ASYNC,
  SAVE_YOUR_TEST_SUCCESS,
  VERIFY_OTP_ACCOUNT_SUCCESS,
  VERIFY_OTP_ACCOUNT_FAILED,
  VERIFY_OTP_ACCOUNT_ASYNC,
  SEND_OTP_VERIFY_ACCOUNT_SUCCESS,
  SEND_OTP_VERIFY_ACCOUNT_FAILED,
  SEND_OTP_VERIFY_ACCOUNT_ASYNC,
} from './contains';
import {
  HIDE_LOADING_OVERLAY,
  SHOW_ALERT,
  SHOW_LOADING_OVERLAY,
} from '../app/constants';
import {
  createInformation,
  getCompanyDropdown,
  getStates,
  saveWorkReference,
  saveYourExperience,
  updateInformation,
  getAllOnboardingQuestion,
  saveYourTest,
  verifyEmailOrPhone,
  sendOTPVerifyAccount,
} from '../../../api/register';
import { ResponseApi } from '../../store/types';
import { tabbarRegisterRef } from '../../../helper/register';
import i18n from '../../../i18n';
import { store } from '../../store';
import { getCompanyAsync } from '../registerCompany/actions';

const fetchGetCompanyDropdown = function* (): Generator<any, void, any> {
  try {
    yield put({
      type: SHOW_LOADING_OVERLAY,
    });
    const response = (yield call(getCompanyDropdown)) as ResponseApi;
    if (response.status === 200) {
      yield put({
        type: GET_COMPANY_DROPDOWN_SUCCESS,
        payload: response.data,
      });
    } else {
      yield put({
        type: GET_COMPANY_DROPDOWN_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({
      type: GET_COMPANY_DROPDOWN_FAILED,
      payload: error,
    });
  } finally {
    yield put({
      type: HIDE_LOADING_OVERLAY,
    });
  }
};

const fetchGetStates = function* (): Generator<any, void, any> {
  try {
    yield put({
      type: GET_STATES_PROGRESS,
      payload: true,
    });
    const response = (yield call(getStates)) as ResponseApi;
    if (response.status === 200) {
      yield put({
        type: GET_STATES_SUCCESS,
        payload: response.data,
      });
    } else {
      yield put({
        type: GET_STATES_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({
      type: GET_STATES_FAILED,
      payload: error,
    });
  }
};

const fetchCreateInformation = function* (action: {
  type: string;
  payload: {
    formData: FormData;
    values: any;
  };
}): Generator<any, void, any> {
  try {
    yield put({
      type: SHOW_LOADING_OVERLAY,
      payload: true,
    });
    const accountId = store.getState().register.createInformation.data;

    let response;
    if (accountId) {
      response = (yield call(
        updateInformation,
        action.payload.formData,
      )) as ResponseApi;
    } else {
      response = (yield call(
        createInformation,
        action.payload.formData,
      )) as ResponseApi;
    }
    if (response.status === 201 || response.status === 200) {
      if (response?.data?.accountId) {
        yield put({
          type: CREATE_INFORMATION_SUCCESS,
          payload: {
            accountId: response?.data?.accountId,
            values: action.payload.values,
          },
        });
      }

      yield call([
        tabbarRegisterRef.current,
        tabbarRegisterRef.current.pressNext,
      ]);
    } else {
      yield put({
        type: SHOW_ALERT,
        payload: {
          title: i18n.t('WARNING'),
          message: response.message,
          type: 'warning',
        },
      });
    }
  } catch (error) {
    yield put({
      type: CREATE_INFORMATION_FAILED,
      payload: error,
    });
  } finally {
    yield put({
      type: HIDE_LOADING_OVERLAY,
    });
  }
};

