const fetchGetAllOnboardingQuestion = function* (action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({
      type: GET_ONBOARDING_QUESTION_PROGRESS,
      payload: true,
    });
    const response = (yield call(
      getAllOnboardingQuestion,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      const payloadReducer = {
        id: action.payload,
        data: response.data,
      };
      yield put({
        type: GET_ONBOARDING_QUESTION_SUCCESS,
        payload: payloadReducer,
      });
    } else {
      yield put({
        type: GET_ONBOARDING_QUESTION_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({
      type: GET_ONBOARDING_QUESTION_FAILED,
      payload: error,
    });
  } finally {
    yield put({
      type: GET_ONBOARDING_QUESTION_PROGRESS,
      payload: false,
    });
  }
};

const fetchSaveYourTest = function* (action: {
  type: string;
  payload: {
    formData: FormData;
    isRegisterCompany?: boolean;
    navigationRef?: any;
  };
}): Generator<any, void, any> {
  const informationData = store.getState().register.createInformation.data;
  const { formData, isRegisterCompany, navigationRef } = action.payload;
  try {
    yield put({
      type: SHOW_LOADING_OVERLAY,
      payload: true,
    });
    const response = (yield call(saveYourTest, formData)) as ResponseApi;
    if (response.status === 200) {
      yield put({
        type: SAVE_YOUR_TEST_SUCCESS,
        payload: response.data,
      });

      if (isRegisterCompany) {
        yield put(getCompanyAsync());
        yield call(navigationRef.goBack());
      } else {
        const responseSendOTPVerifyAccount = (yield call(sendOTPVerifyAccount, {
          receiveType: 1,
          email: informationData?.values?.EMAIL ?? '',
        })) as ResponseApi;

        if (responseSendOTPVerifyAccount.status === 200) {
          yield put({
            type: SEND_OTP_VERIFY_ACCOUNT_SUCCESS,
            payload: {
              type: 'verifyPhone',
              data: responseSendOTPVerifyAccount.data,
            },
          });
        } else {
          yield put({
            type: SEND_OTP_VERIFY_ACCOUNT_FAILED,
            payload: {
              type: 'verifyPhone',
              error: responseSendOTPVerifyAccount.message,
            },
          });
        }
        yield call([
          tabbarRegisterRef.current,
          tabbarRegisterRef.current.pressNext,
        ]);
      }
    } else {
      yield put({
        type: SAVE_YOUR_TEST_FAILED,
        payload: response.message,
      });
      yield put({
        type: SHOW_ALERT,
        payload: {
          title: i18n.t('WARNING'),
          message: response.message,
          type: 'warning',
        },
      });
    }
  } catch (error) {
    yield put({
      type: SAVE_YOUR_TEST_FAILED,
      payload: error,
    });
  } finally {
    yield put({
      type: HIDE_LOADING_OVERLAY,
      payload: false,
    });
  }
};

const fetchVerifyOtpAccount = function* (action: {
  type: string;
  payload: {
    isEmail: boolean;
    email: string;
    phone: string;
    otpCode: string;
  };
}): Generator<any, void, any> {
  try {
    yield put({
      type: SHOW_LOADING_OVERLAY,
      payload: true,
    });
    const response = (yield call(
      verifyEmailOrPhone,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield put({
        type: VERIFY_OTP_ACCOUNT_SUCCESS,
        payload: {
          type: action.payload.isEmail ? 'verifyEmail' : 'verifyPhone',
          data: response.data,
        },
      });

      if (!response.data?.isEmail) {
        const informationData =
          store.getState().register.createInformation.data;

        const responseSendOTPVerifyAccount = (yield call(sendOTPVerifyAccount, {
          receiveType: 0,
          email: informationData?.values?.EMAIL ?? '',
        })) as ResponseApi;

        if (responseSendOTPVerifyAccount.status === 200) {
          yield put({
            type: SEND_OTP_VERIFY_ACCOUNT_SUCCESS,
            payload: {
              type: 'verifyEmail',
              data: responseSendOTPVerifyAccount.data,
            },
          });
        } else {
          yield put({
            type: SEND_OTP_VERIFY_ACCOUNT_FAILED,
            payload: {
              type: 'verifyEmail',
              error: responseSendOTPVerifyAccount.message,
            },
          });
        }
      }
    } else {
      yield put({
        type: VERIFY_OTP_ACCOUNT_FAILED,
        payload: {
          type: action.payload.isEmail ? 'verifyEmail' : 'verifyPhone',
          error: response.message,
        },
      });
    }
  } catch (error) {
    yield put({
      type: VERIFY_OTP_ACCOUNT_FAILED,
      payload: {
        type: action.payload.isEmail ? 'verifyEmail' : 'verifyPhone',
        error: error,
        data: null,
      },
    });
  } finally {
    yield put({
      type: HIDE_LOADING_OVERLAY,
      payload: false,
    });
  }
};

const fetchSendOTPVerifyAccount = function* (action: {
  type: string;
  payload: {
    receiveType: 1 | 0;
    email: string;
  };
}): Generator<any, void, any> {
  try {
    yield put({
      type: SHOW_LOADING_OVERLAY,
      payload: true,
    });
    const response = (yield call(
      sendOTPVerifyAccount,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield put({
        type: SEND_OTP_VERIFY_ACCOUNT_SUCCESS,
        payload: {
          type:
            action.payload.receiveType === 0 ? 'verifyEmail' : 'verifyPhone',
          data: response.data,
        },
      });
    } else {
      yield put({
        type: SEND_OTP_VERIFY_ACCOUNT_FAILED,
        payload: {
          type:
            action.payload.receiveType === 0 ? 'verifyEmail' : 'verifyPhone',
          error: response.message,
          data: null,
        },
      });
    }
  } catch (error) {
    yield put({
      type: SEND_OTP_VERIFY_ACCOUNT_FAILED,
      payload: {
        type: action.payload.receiveType === 0 ? 'verifyEmail' : 'verifyPhone',
        error: error,
        data: null,
      },
    });
  } finally {
    yield put({
      type: HIDE_LOADING_OVERLAY,
      payload: false,
    });
  }
};

export default function* registerSaga() {
  yield takeEvery(GET_COMPANY_DROPDOWN_ASYNC, fetchGetCompanyDropdown);
  yield takeEvery(GET_STATES_ASYNC, fetchGetStates);
  yield takeEvery(CREATE_INFORMATION_ASYNC, fetchCreateInformation);
  yield takeEvery(SAVE_YOUR_EXPERIENCE_ASYNC, fetchSaveYourExperience);
  yield takeEvery(SAVE_WORK_REFERENCE_ASYNC, fetchSaveWorkReference);
  yield takeEvery(GET_ONBOARDING_QUESTION_ASYNC, fetchGetAllOnboardingQuestion);
  yield takeEvery(SAVE_YOUR_TEST_ASYNC, fetchSaveYourTest);
  yield takeEvery(VERIFY_OTP_ACCOUNT_ASYNC, fetchVerifyOtpAccount);
  yield takeEvery(SEND_OTP_VERIFY_ACCOUNT_ASYNC, fetchSendOTPVerifyAccount);
}
