import {
  BANK_INFO_ASYNC,
  BANK_INFO_FAILED,
  BANK_INFO_SUCCESS,
  BANK_INFO_RESET,
} from './contants';
import { PaymentInfoW9Data } from './types';

// action saga
export const bankInfoAsync = (payload: string) =>
  ({ type: BANK_INFO_ASYNC, payload } as const);

// action reducer

export const bankInfoSuccess = (payload: PaymentInfoW9Data) =>
  ({ type: BANK_INFO_SUCCESS, payload } as const);
export const bankInfoFailed = (payload: string) =>
  ({ type: BANK_INFO_FAILED, payload } as const);
export const bankInfoReset = () => ({ type: BANK_INFO_RESET } as const);

export const actionPaymentInfoW9 = {
  bankInfoAsync,
  bankInfoSuccess,
  bankInfoFailed,
  bankInfoReset,
};

export type PaymentInfoW9Action = ReturnType<
  | typeof bankInfoAsync
  | typeof bankInfoSuccess
  | typeof bankInfoFailed
  | typeof bankInfoReset
>;
