export const createInformation = async (formData: FormData) => {
  try {
    const response = await client.post(createInformationEndpoint, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: undefined,
      },
    });

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchCreateInformation', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

