export const saveYourTest = async (formData: FormData) => {
  try {
    const response = await client.put(saveYourTestEndpoint, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        accept: 'text/plain',
        'accept-language': 'en',
      },
    });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error saveYourTest', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

