export const tabbarRegisterRef = createRef<any>();
export type CompanyAnswer = {
  companyKey: string;
  answer: 'yes' | 'no' | null;
};

function getDeletedExperienceIds(oldData: any, newData: any) {
  try {
    const result: any = {
      deleteIds: [],
      projectDeleteIds: [],
    };

    // Tạo Set các ID hiện tại để tra cứu nhanh
    const currentExperienceIds = new Set(
      newData.data.map((exp: any) => exp.id),
    );
    const currentProjectIds = new Set();

    newData.data.forEach((exp: any) => {
      exp.projectDetails.forEach((proj: any) => {
        currentProjectIds.add(proj.id);
      });
    });

    // Tìm experience bị xóa
    oldData.data.forEach((oldExp: any) => {
      if (!currentExperienceIds.has(oldExp.id)) {
        result.deleteIds.push(oldExp.id);
      }
    });

    // Tìm project bị xóa
    oldData.data.forEach((oldExp: any) => {
      oldExp.projectDetails.forEach((oldProj: any) => {
        if (!currentProjectIds.has(oldProj.id)) {
          result.projectDeleteIds.push(oldProj.id);
        }
      });
    });

    return result;
  } catch (error) {
    logger('error get deleted ids', error);
    return null;
  }
}

function getDeletedWorkReferenceIds(oldData: any, newData: any) {
  const deleteIds: any = [];

  // Tạo Set các ID hiện tại
  const currentIds = new Set(newData.data.map((item: any) => item.id));

  // Tìm các ID bị xóa
  oldData.data.forEach((oldItem: any) => {
    if (!currentIds.has(oldItem.id)) {
      deleteIds.push(oldItem.id);
    }
  });

  return { deleteIds };
}

export const getUpdatedSections = (
  states: { value: string; label: string }[],
) => {
  return SECTIONS_LIST_ITEMS.map(section => {
    if (section.title !== 'LOCATION') return section;

    return {
      ...section,
      data: section.data.map(item => {
        if (!('field' in item)) return item;
        return {
          ...item,
          field: ('field' in item ? item.field : []).map((field: any) => {
            if (field.label === 'STATE') {
              return { ...field, options: states };
            }
            return field;
          }),
        };
      }),
    };
  });
};

export const handleTakePhoto = async (
  setFieldValue: (
    field: string,
    value: { uri: string; type: string; fileName: string },
  ) => void,
  setImg: (img: { uri: string; error: string }) => void,
) => {
  try {
    const image = await ImagePicker.openCamera({
      cropping: false,
      includeExif: true,
      mediaType: 'photo',
      forceJpg: true,
      includeBase64: false,
    });

    if (image?.path) {
      setFieldValue('PHOTO_IDENTIFICATION', {
        uri: image?.path,
        type:
          image?.mime ||
          mime.getType(image?.path) ||
          `.${image?.path?.split('.').pop()}` ||
          '',
        fileName: image?.filename || image?.path?.split('/').pop() || '',
      });
      setImg({ uri: image?.path, error: '' });
    } else {
      setImg({ uri: '', error: 'ERROR_TAKE_PHOTO' });
    }
  } catch (error) {
    logger('error take photo', error);
  }
};

export const handleSubmitInformation = (values: typeof initialValues) => {
  try {
    const getState = store.getState().register.getStates.data;
    const statedSelected = getState.find(
      (state: { label: string }) => state.label === values.STATE,
    );
    const referralSelected = referralOptions.find(
      (referral: { label: string }) => referral.label === values.REFERRAL,
    );
    const identificationPhoto = {
      uri: values.PHOTO_IDENTIFICATION?.uri,
      type: values.PHOTO_IDENTIFICATION?.type,
      name: values.PHOTO_IDENTIFICATION?.fileName,
    };

    const formData = new FormData();
    //info
    formData.append('FirstName', values.FIRST_NAME);
    formData.append('LastName', values.LAST_NAME);
    formData.append('Phone', values.PHONE_NO);
    formData.append('Email', values.EMAIL);
    //location
    formData.append('Street', values.STREET_ADDRESS);
    formData.append('City', values.CITY);
    formData.append('State', statedSelected?.value);
    formData.append('ZipCode', values.ZIP_CODE);
    //referral
    formData.append('Referral', referralSelected?.value);
    if (values.FRIEND_NAME) {
      formData.append('FriendName', values.FRIEND_NAME);
    }
    //social security
    formData.append('SSNOrITIN', values.SSN_ITIN);
    if (values.DATE_OF_BIRTH) {
      const birthday = dayjs(values.DATE_OF_BIRTH).toISOString();
      formData.append('Birthday', birthday);
    }
    formData.append(
      'IdentificationNumbers[0].identificationNumberType',
      values.IDENTIFICATION_TYPE,
    );
    formData.append(
      'IdentificationNumbers[0].identificationNumber',
      values.IDENTIFICATION_NUMBER,
    );
    formData.append(
      'IdentificationNumbers[0].identificationNumberPhoto',
      identificationPhoto,
    );
    formData.append('UserTypes', [0]);

    store.dispatch(createInformationAsync({ formData, values }));
  } catch (error) {
    logger('error submit information', error);
  }
};

export const handleSubmitExperience = (
  values: typeof initialValuesExperience,
) => {
  try {
    const objectValues = values?.companyInformation;
    const accountId =
      store.getState().register.createInformation.data?.accountId;
    const data = objectValues?.map((item: any) => {
      return {
        id: item.ID,
        accountId,
        company: item.COMPANY_NAME,
        companyAddress: item.COMPANY_ADDRESS,
        from: dayjs(item.START_DATE).toISOString() || '',
        to: dayjs(item.END_DATE).toISOString() || '',
        role: item.ROLE,
        jobDescription: item.JOB_DESCRIPTION,
        projectDetails: item.PROJECTS?.map((element: any) => {
          return {
            id: element.ID,
            projectName: element.COMPANY_PROJECT_NAME,
            projectDescription: element.COMPANY_PROJECT_DESCRIPTION,
          };
        }),
      };
    });
    const payloadObj = {
      data,
      deleteIds: [],
      projectDeleteIds: [],
    };

    const formRequested =
      store.getState().register.saveYourExperience.formRequested;
    let payload;
    if (formRequested) {
      const deletedIds = getDeletedExperienceIds(formRequested, payloadObj);
      payload = {
        ...payloadObj,
        deleteIds: deletedIds?.deleteIds,
        projectDeleteIds: deletedIds?.projectDeleteIds,
      };
    } else {
      payload = payloadObj;
    }

    store.dispatch(saveYourExperienceAsync(payload));
  } catch (error) {
    logger('error submit experience', error);
  }
};

export const handleSubmitWorkReferences = (
  values: typeof initialValuesWorkReferences,
) => {
  try {
    const accountId =
      store.getState().register.createInformation.data?.accountId;
    const formRequested =
      store.getState().register.saveWorkReference.formRequested;
    const data = values?.references?.map((item: any) => {
      return {
        id: item.ID,
        companyName: item.COMPANY_NAME,
        companyAddress: item.COMPANY_ADDRESS,
        referenceName: item.REFERENCE_NAME,
        phone: item.REFERENCE_PHONE,
        email: item.REFERENCE_EMAIL,
        relationship: item.RELATIONSHIP,
      };
    });

    const payloadObj = {
      accountId,
      data,
      deleteIds: [],
    };

    let payload;

    if (formRequested) {
      const deletedIds = getDeletedWorkReferenceIds(formRequested, payloadObj);
      payload = {
        accountId,
        data,
        ...deletedIds,
      };
    } else {
      payload = {
        accountId,
        data,
        deleteIds: [],
      };
    }
    store.dispatch(saveWorkReferenceAsync(payload));
  } catch (error) {
    logger('error submit work references', error);
  }
};

export const handleUploadMedia = async ({
  images = [],
  setImages,
  companyKey,
  type = 'picker',
}: {
  images: any[];
  setImages: (images: any) => void;
  companyKey: string;
  type: 'picker' | 'takePhoto';
}) => {
  try {
    const maxFiles =
      images?.find(item => item.companyKey === companyKey)?.images?.length || 0;

    let dataSave: any = null;

    if (type === 'picker') {
      const image = await ImagePicker.openPicker({
        multiple: true,
        maxFiles: 5 - maxFiles,
        mediaType: 'photo',
        forceJpg: true,
        includeBase64: false,
        includeExif: true,
      });
      dataSave = {
        companyKey,
        images: image?.map((item: any) => ({
          id: `${companyKey}_${Date.now()}_${Math.random()
            .toString(36)
            .substr(2, 9)}`,
          uri: item?.path,
          type: item?.mime,
          fileName: item?.filename,
        })),
      };
    } else {
      const image = await ImagePicker.openCamera({
        cropping: false,
        includeExif: true,
        mediaType: 'photo',
        forceJpg: true,
        includeBase64: false,
      });

      dataSave = {
        companyKey,
        images: [
          {
            id: `${companyKey}_${Date.now()}_${Math.random()
              .toString(36)
              .substr(2, 9)}`,
            uri: image?.path,
            type: image?.mime,
            fileName: image?.filename,
          },
        ],
      };
    }
    if (dataSave) {
      setImages((prevImages: any) => {
        const existingData = prevImages.find(
          (item: any) => item.companyKey === companyKey,
        );
        if (existingData) {
          return prevImages.map((item: any) =>
            item.companyKey === companyKey
              ? { ...item, images: [...item.images, ...dataSave.images] }
              : item,
          );
        }
        return [...prevImages, dataSave];
      });
    }
  } catch (error) {
    logger('error handleUploadMedia', error);
  }
};

export const handleDeleteImage = ({
  companyKey,
  imageId,
  setImages,
}: {
  companyKey: string;
  imageId: string;
  setImages: (images: any) => void;
}) => {
  setImages((prevImages: any) => {
    return prevImages
      .map((item: any) => {
        if (item.companyKey === companyKey) {
          const updatedImages = item.images.filter(
            (img: any) => img.id !== imageId,
          );

          // If no images left, remove the entire company entry
          if (updatedImages.length === 0) {
            return null;
          }

          return {
            ...item,
            images: updatedImages,
          };
        }
        return item;
      })
      .filter(Boolean); // Remove null entries
  });
};

export const getNecessaryTool = (
  imagesCompanyAnswers: any[],
  companyAnswers: CompanyAnswer[],
) => {
  try {
    const companyDropdown = store.getState().register.getCompanyDropdown.data;
    const mergedArray = companyDropdown?.map((ans: any) => {
      const company = companyAnswers.find(
        (item: any) => item.companyKey === ans.key,
      );
      const companyNecessaryToolImages = imagesCompanyAnswers.find(
        (item: any) => item.companyKey === ans.key,
      );

      return {
        companyKey: ans.key,
        answer: ans.isChecked ? company?.answer || 'no' : null,
        images: companyNecessaryToolImages?.images || [],
        companyName: ans.value,
      };
    });

    const checkRequiredImages = mergedArray
      .filter((item: any) => item.answer === 'yes' && item.images.length === 0)
      ?.map((item: any) => item.companyName);

    const checkRequiredImagesIds = mergedArray
      .filter((item: any) => item.answer === 'yes' && item.images.length === 0)
      ?.map((item: any) => item.companyKey);

    if (checkRequiredImages?.length > 0) {
      store.dispatch(setErrorQuestion(checkRequiredImagesIds));
      return store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('YOU_MUST_UPLOAD_IMAGE_NECESSARY_TOOL_FOR_COMPANY', {
            companyName: checkRequiredImages.join(', '),
          }),
          type: 'warning',
          buttonText: i18n.t('CLOSE'),
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
    }

    store.dispatch(clearErrorQuestion());

    return mergedArray?.filter((item: any) => item.answer !== null);
  } catch (error) {
    logger('error get necessary tool', error);
  }
};

export const handleSubmit = (
  imagesCompanyAnswers: any[],
  companyAnswers: CompanyAnswer[],
) => {
  try {
    const formData = new FormData();
    const accountId =
      store.getState().register.createInformation.data?.accountId;
    formData.append(
      'AccountId',
      accountId || 'ab8c4351-fde3-4ab1-a172-c61985f652f1',
    );
    formData.append('IsCrewLead', false);

    const onboardingQuestionData =
      store.getState().register.getAllOnboardingQuestion?.data;
    const questionAnswers = store.getState().register.questionAnswers;
    const companyDropdownData =
      store.getState().register.getCompanyDropdown.data;
    const necessaryToolData = getNecessaryTool(
      imagesCompanyAnswers,
      companyAnswers,
    );

    if (necessaryToolData && necessaryToolData?.length === 0) {
      return store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('YOU_MUST_SELECT_AT_LEAST_ONE_COMPANY'),
          type: 'warning',
          buttonText: i18n.t('CLOSE'),
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
    }
    necessaryToolData.forEach((element: any, index: number) => {
      formData.append(`NecessaryTool[${index}].companyId`, element.companyKey);
      formData.append(`NecessaryTool[${index}].question`, element.companyName);
      formData.append(
        `NecessaryTool[${index}].necessary`,
        element.answer === 'yes' ? 'true' : 'false',
      );

      element.images.forEach((img: any, imgIndex: number) => {
        formData.append(`NecessaryTool[${index}].images[${imgIndex}]`, {
          uri: img.uri,
          type: img.type,
          name: img.fileName,
        });
      });
    });

    const selectedCompanyIds = companyDropdownData
      ?.filter((item: any) => item.isChecked)
      ?.map((item: any) => item.key);

    const mergedArrayQuestion = onboardingQuestionData
      ?.filter((group: any) => selectedCompanyIds.includes(group.id))
      ?.map((group: any) => ({
        ...group,
        data: group?.data?.map((q: any) => {
          const foundQuestion = questionAnswers.find(
            ans => ans.questionId === q.id,
          );
          return {
            ...q,
            options: q.options.map((opt: any) => {
              const found = questionAnswers.find(
                ans => ans.questionId === q.id && ans.optionId === opt.optionId,
              );
              return {
                ...opt,
                isChecked: found ? found.isChecked : false,
              };
            }),
            images: foundQuestion?.images || q.images || [],
          };
        }),
      }));

    const uncheckedQuestionIds = mergedArrayQuestion?.flatMap((item: any) =>
      item?.data
        ?.filter((q: any) => q.options.every((opt: any) => !opt.isChecked))
        ?.map((q: any) => q.id),
    );

    if (uncheckedQuestionIds?.length > 0) {
      store.dispatch(setErrorQuestion(uncheckedQuestionIds));
      return store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('YOU_MUST_ANSWER_ALL_THE_QUESTIONS'),
          type: 'warning',
          buttonText: i18n.t('CLOSE'),
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
    }

    store.dispatch(clearErrorQuestion());

    const invalidImageItems = mergedArrayQuestion?.flatMap((group: any) =>
      group.data
        ?.filter((q: any) => {
          const selectedOpt = q.options.find((opt: any) => opt.isChecked);
          return selectedOpt?.requiredImage && q.images?.length === 0;
        })
        ?.map((q: any) => q.id),
    );

    if (invalidImageItems?.length > 0) {
      store.dispatch(setErrorQuestion(invalidImageItems));
      return store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('HAVE_QUESTION_TO_ADD_IMAGE'),
          type: 'warning',
          buttonText: i18n.t('CLOSE'),
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
    }
    store.dispatch(clearErrorQuestion());

    mergedArrayQuestion?.forEach((element: any, index: number) => {
      formData.append(`Tests[${index}].CompanyId`, element.id);
      formData.append(
        `Tests[${index}].CompanyName`,
        companyDropdownData?.find((item: any) => item.key === element.id)
          ?.value,
      );
      element.data.forEach((q: any, qIndex: number) => {
        formData.append(
          `Tests[${index}].Questions[${qIndex}].QuestionId`,
          q.id,
        );
        formData.append(
          `Tests[${index}].Questions[${qIndex}].QuestionName`,
          q.question,
        );
        if (q.isRequired) {
          formData.append(
            `Tests[${index}].Questions[${qIndex}].IsRequired`,
            q.isRequired.toString(),
          );
        }
        q.options.forEach((opt: any, optIndex: number) => {
          formData.append(
            `Tests[${index}].Questions[${qIndex}].ListAnswers[${optIndex}].key`,
            opt.optionId,
          );
          formData.append(
            `Tests[${index}].Questions[${qIndex}].ListAnswers[${optIndex}].Value`,
            opt.option,
          );
        });
        q.options.forEach((opt: any, optIndex: number) => {
          if (opt.isChecked) {
            formData.append(
              `Tests[${index}].Questions[${qIndex}].Answers[${optIndex}].key`,
              q.optionId,
            );
            formData.append(
              `Tests[${index}].Questions[${qIndex}].Answers[${optIndex}].Value`,
              opt.option,
            );
          }
        });

        if (q.images?.length > 0) {
          q.images.forEach((img: any, imgIndex: number) => {
            formData.append(
              `Tests[${index}].Questions[${qIndex}].Files[${imgIndex}]`,
              {
                uri: img.path,
                type: img.typeFile,
                name: img.fileName,
              },
            );
          });
        }
      });

      store.dispatch(
        saveYourTestAsync({
          formData,
          isRegisterCompany: false,
        }),
      );
    });
  } catch (error) {
    logger('error submit ', error);
  }
};

export const handleVerifyOtpAccount = ({
  type,
  otp,
}: {
  type: 'email' | 'phone';
  otp: string;
}) => {
  try {
    const informationData = store.getState().register.createInformation.data;

    if (!otp || otp.length < 6) {
      return Toast.show({
        text1: i18n.t('PLEASE_ENTER_THE_NUMBER_DIGITS', { number: 6 }),
        type: 'error',
      });
    }

    const payload = {
      isEmail: type === 'email',
      email: informationData?.values?.EMAIL,
      phone: informationData?.values?.PHONE_NO,
      otpCode: otp,
    };

    store.dispatch(verifyOtpAccountAsync(payload));
  } catch (error) {
    logger('error handleVerifyOtpAccount', error);
  }
};
