import { client } from './configAxios';
import { logger } from '../utils';
import { getInfoBankAccount } from './endpoint';

export const fetchPaymentInfoW9 = async () => {
  try {
    const response = await client.get(getInfoBankAccount);

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchForgotPassword', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
