import { combineReducers } from 'redux';
import appReducer from '../features/app/reducer';
import authReducer from '../features/auth/reducer';
import forgotPasswordReducer from '../features/forgotPassword/reducer';
import verifyOtpReducer from '../features/verifyOtp/reducer';
import changePasswordReducer from '../features/changePassword/reducer';
import registerReducer from '../features/register/reducer';
import homeReducer from '../features/home/reducer';
import referFriendReducer from '../features/referFriend/reducer';
import networkReducer from '../features/network/reducer';
import userReducer from '../features/user/reducer';
import contractReducer from '../features/contract/reducer';
import roleTypeReducer from '../features/roleType/reducer';
import registerCompanyReducer from '../features/registerCompany/reducer';
import paymentInfoW9Reducer from '../features/paymentInfoW9/reducer';

const rootReducer = combineReducers({
  app: appReducer,
  auth: authReducer,
  forgotPassword: forgotPasswordReducer,
  verifyOtp: verifyOtpReducer,
  changePassword: changePasswordReducer,
  register: registerReducer,
  home: homeReducer,
  referFriend: referFriendReducer,
  network: networkReducer,
  user: userReducer,
  contract: contractReducer,
  roleType: roleTypeReducer,
  registerCompany: registerCompanyReducer,
  paymentInfoW9: paymentInfoW9Reducer,
});

export type RootState = ReturnType<typeof rootReducer>;
export default rootReducer;
