import {
  BANK_INFO_FAILED,
  BANK_INFO_RESET,
  BANK_INFO_SUCCESS,
} from './contants';
import { PaymentInfoW9Action } from './actions';
import { PaymentInfoW9State } from './types';

export const initialState: PaymentInfoW9State = {
  data: null,
};

export default function forgotPasswordReducer(
  state = initialState,
  action: PaymentInfoW9Action,
): PaymentInfoW9State {
  switch (action.type) {
    case BANK_INFO_SUCCESS:
      return {
        ...state,
        data: action.payload,
      };
    case BANK_INFO_FAILED:
      return {
        ...state,
        isLoading: false,
        error: action.payload,
        success: false,
      };
    case BANK_INFO_RESET:
      return { ...initialState };
    default:
      return state;
  }
}
